<template>
   <div class="ContVideoLive">
      <div class="Bx-CountAlert">
         <div class="BxCountDownt">
            <div class="countdowns">
               <Countdown
                v-on:event_child="handleExit2"
                :startTime = datetimestart
                :endTime= datetimeend
                trans='{
                    "day":"Day",
                    "hours":"Hours",
                    "minutes":"Minuets",
                    "seconds":"Seconds",
                    "expired":"Event has been expired.",
                    "running":"Till the end of event.",
                    "upcoming":"Till start of event.",
                    "status": {
                        "expired":"Session Expired",
                        "running":"Session is Running",
                        "upcoming":"Upcoming Session"
                    }
                }'  
               />
            </div>
           
         </div>
      </div>
      <v-snackbar
         v-model="NotifWaiting"
         color="blue darken-3"
         :timeout="timeout"
         top
         style="margin:auto;"
         class="SesNotif"
         v-if="this.streamList.length <= 1"
         >
            <p v-if="!AuthCoach"  style="margin-bottom:0px;text-align:center;width: 100%;font-style:italic;font-size:.9em;">Waiting for Coach to Join the Session, Please remain in session until the end in order to receive a refund of your tokens</p>

            <p v-if="AuthCoach" style="margin-bottom:0px;text-align:center;width: 100%;font-style:italic;font-size:.9em;">Waiting for Student to Join the Session, Please remain in session until the end to receive tokens.</p>
      </v-snackbar>
      <v-btn class="btnRound liveBtn BtnShare" @click="StopScreenSharing" v-if="AuthCoach && ShareScreen">
         Stop Screen Sharing
      </v-btn>
      <div id="ag-canvas" class="BX-agcanvas"></div>
      <section class="bxButton">
         <div class="ag-btn-group">
            <!-- <span 
               class="ag-btn exitBtn"
               title="Exit"
               @click="handleExit"
               :class="{'disabled': !readyState}"
            >
               <i class="ag-icon ag-icon-leave"></i>
            </span> -->

             <!-- <span 
               v-if="AuthCoach"
               class="ag-btn displayModeBtn"
               title="Status Record"
               @click="StatusRecord"
            >
               <i class="ag-icon ag-icon-switch-display"></i>
            </span> -->

            <span 
               class="ag-btn videoControlBtn" 
               title="Enable/Disable Video"
               v-if="attendeeMode === 'video'"
               @click="handleCamera"
            >
               <i class="ag-icon ag-icon-camera"></i>
               <i class="ag-icon ag-icon-camera-off"></i>
            </span>

            <span
               class="ag-btn audioControlBtn" 
               title="Enable/Disable Audio"
               v-if="attendeeMode !== 'audience'"
               @click="handleMic"
            >
               <i class="ag-icon ag-icon-mic"></i>
               <i class="ag-icon ag-icon-mic-off"></i>
            </span>

            <!-- <span 
               class="ag-btn displayModeBtn"
               title="Switch Display Mode"
               @click="switchDisplay"
               :class="{'disabled': streamList.length > 4}"
            >
               <i class="ag-icon ag-icon-switch-display"></i>
            </span> -->

            <span 
               class= "ag-btn disableRemoteBtn"
               title="Hide Remote Stream"
               @click="hideRemote"
            >
                <i class="ag-icon ag-icon-switch-display"></i>
            </span>

            <span 
               v-if="AuthCoach"
               class= "ag-btn "
               title="Enable/Disable Screen Sharing"
               @click="ScreenSharing"
               :class="{'disabled': streamList.length == 1 || streamList.length == 3 }"
            >
              <i class="ag-icon ag-icon-remove-pip"></i>
            </span>

         </div>
      </section>
     
   </div>
</template>

<script>
   import moment from 'moment'
   import Countdown from './countdown/Countdown.vue'
   import AgoraRTC from 'agora-rtc-sdk'
   import {merge} from 'lodash'

   const tile_canvas = {
      "1": ["span 12/span 24"],
      "2": ["span 12/span 12/13/25", "span 12/span 12/13/13"],
      "3": ["span 6/span 12", "span 6/span 12", "span 6/span 12/7/19"],
      "4": [
         "span 6/span 12",
         "span 6/span 12",
         "span 6/span 12",
         "span 6/span 12/7/13"
      ],
      "5": [
         "span 3/span 4/13/9",
         "span 3/span 4/13/13",
         "span 3/span 4/13/17",
         "span 3/span 4/13/21",
         "span 9/span 16/10/21"
      ],
      "6": [
         "span 3/span 4/13/7",
         "span 3/span 4/13/11",
         "span 3/span 4/13/15",
         "span 3/span 4/13/19",
         "span 3/span 4/13/23",
         "span 9/span 16/10/21"
      ],
      "7": [
         "span 3/span 4/13/5",
         "span 3/span 4/13/9",
         "span 3/span 4/13/13",
         "span 3/span 4/13/17",
         "span 3/span 4/13/21",
         "span 3/span 4/13/25",
         "span 9/span 16/10/21"
      ]
   };
   export default {
      components:{
         Countdown
      },
      data() {
         return {
            UIDS:'',
            AuthCoach:false,
            client: {},
            ShareScreen: null,
            ShareScreenUID:null,
            localStream: {},
            shareClient: {},
            shareStream: {},
            displayMode: "pip",
            streamList: [],
            readyState: false,
            datetimestart:'',
            datetimeend: '',
            datetimestarts:'16 april, 2019 17:41:00',
            datetimeends: '16 april, 2019 18::00',
            NotifWaiting: true,
            timeout: 6000000,
           
         };
      },

      props: [
         "transcode",
         "attendeeMode",
         "videoProfile",
         "channel",
         "baseMode",
         "appId",
         "uid",
         "GetSessionStudent",
         "DataUpcoming",
         "UidRecord"
      ],

      methods: {
         eventChild:(($event)=>{
            console.log($event)
           this.handleExit()
         }),

         StopScreenSharing(){
            this.shareClient && this.shareClient.unpublish(this.shareStream);
            this.shareStream && this.shareStream.close();
            this.shareClient &&
               this.shareClient.leave(
                  () => {
                  console.log("Share Screen Success Remove.");
                  },
                  () => {
                  console.log("Share Screen failed to Remove.");
                  }
               );

            this.UpdateLayoutEnd()
            this.readyState = false;
            this.shareClient = null;
            this.shareStream = null;
            this.ShareScreen = false;
           
         },

         AuthRecording(){
            this.$store.commit('MuttAgoraInfo',{
               channel : this.channel,
               status  : 'Join Success',
               uid :     this.UidRecord.toString() + "8"
            })     
            this.$store.dispatch('POST_ACQUIRE')
         },

         UpdateLayout(uid){
            let uids = uid
            this.ShareScreenUID = uid
            const DataLayout = {
               uid : this.$store.state.AgoraInfo.uid,
               cname : this.$store.state.AgoraInfo.channel,
               clientRequest: {
                  mixedVideoLayout : 2,
                  maxResolutionUid : uids.toString()
               }
            }
            this.$store.dispatch('UPDATE_LAYOUT', DataLayout)
         },

         UpdateLayoutEnd(){
            let uid =  this.ShareScreenUID 
            const DataLayout = {
               uid : this.$store.state.AgoraInfo.uid,
               cname : this.$store.state.AgoraInfo.channel,
               clientRequest: {
                  mixedVideoLayout : 1,
                  maxResolutionUid : uid.toString()
               }
            }
            this.$store.dispatch('UPDATE_LAYOUT', DataLayout)
         },

         StatusRecord(){
            this.$store.dispatch('GET_RECORDING_STATUS')
         },

         streamInitShare(uid, attendeeMode, videoProfile, config) {
            let defaultConfig = {
               streamID: 'SHARES',
               audio: false,
               video: false,
               screen: true,
               // extensionId: 'minllpmhdgpndnkomcoccfekfegnlikg',
               mediaSource: 'window'
            };

            let stream = AgoraRTC.createStream(merge(defaultConfig, config));
            stream.setVideoProfile(videoProfile);
            return stream;
         },
         
         streamInit(uid, attendeeMode, videoProfile, config) {
            let defaultConfig = {
               streamID: uid,
               audio: true,
               video: true,
            };

            switch (attendeeMode) {
               case "audio-only":
                  defaultConfig.video = false;
                  break;
               case "audience":
                  defaultConfig.video = false;
                  defaultConfig.audio = false;
                  break;
               default:
               case "video":
                  break;
            }

            let stream = AgoraRTC.createStream(merge(defaultConfig, config));
            stream.setVideoProfile(videoProfile);
            return stream;
         },

         subscribeStreamEvents() {
            let rt = this;
            rt.client.on("stream-added", function(evt) {
               let stream = evt.stream;
               console.log("New stream added: " + stream.getId());
               console.log("At " + new Date().toLocaleTimeString());
               console.log("Subscribe ", stream);
               rt.client.subscribe(stream, function(err) {
                  console.log("Subscribe stream failed", err);
               });
            });

            rt.client.on("peer-leave", function(evt) {
               console.log("Peer has left: " + evt.uid);
               console.log(new Date().toLocaleTimeString());
               //console.log(evt);
               rt.removeStream(evt.uid);
            });

            rt.client.on("stream-subscribed", function(evt) {
            let stream = evt.stream;
               const uid = stream.getId()
               console.log("Got stream-subscribed event");
               console.log(new Date().toLocaleTimeString());
               console.log("Subscribe remote stream successfully: " + stream.getId());
               
              
               console.log(evt);
               rt.addStream(stream);

               
            });

            rt.client.on("stream-removed", function(evt) {
               let stream = evt.stream;
               console.log("Stream removed: " + stream.getId());
               console.log(new Date().toLocaleTimeString());
               //console.log(evt);
               rt.removeStream(stream.getId());
            });

         },

         removeStream(uid) {
            this.streamList.map((item, index) => {
            if (item.getId() === uid) {
               item.close();
               let element = document.querySelector("#ag-item-" + uid);
               if (element) {
                  element.parentNode.removeChild(element);
               }
               let tempList = [...this.streamList];
               tempList.splice(index, 1);
               this.streamList = tempList;
            }
            });
         },

         addStream(stream, push = false) {
            let repeatition = this.streamList.some(item => {
               return item.getId() === stream.getId();
            });
            if (repeatition) {
               return;
            }
            if (push) {
               this.streamList = this.streamList.concat([stream]);
            } else {
               this.streamList = [stream].concat(this.streamList);
            }
         },

         handleCamera(e) {
            e.currentTarget.classList.toggle("off");
            this.localStream.isVideoOn()
            ? this.localStream.muteVideo()
            : this.localStream.unmuteVideo();
         },

         handleMic(e) {
            e.currentTarget.classList.toggle("off");
            this.localStream.isAudioOn()
            ? this.localStream.muteAudio()
            : this.localStream.unmuteAudio();
         },
         
         HandleStopPlayStrim(e) {
            this.localStream.isPlaying()
            ? this.localStream.stop() 
            : this.localStream.play(this.localStream.elementID) 
            console.log(this.localStream)
         },

         switchDisplay(e) {
            if (
            e.currentTarget.classList.contains("disabled") ||
            this.streamList.length <= 1
            ) {
               return;
            }
            if (this.displayMode === "pip") {
               isplayMode = "tile";
            } else if (this.displayMode === "tile") {
               this.displayMode = "pip";
            } else if (this.displayMode === "share") {
            // do nothing or alert, tbd
            } else {
               console.error("Display Mode can only be tile/pip/share");
            }
         },

         hideRemote(e) {
            if (
            e.currentTarget.classList.contains("disabled") ||
            this.streamList.length <= 1
            ) {
               return;
            }
            let list;
            let id = this.streamList[this.streamList.length - 2].getId();
            list = Array.from(
            document.querySelectorAll(`.ag-items:not(#ag-item-${id})`)
            );
            list.map(item => {
            if (item.style.display !== "none") {
               item.style.display = "none";
            } else {
               item.style.display = "block";
            }
            });
         },

         handleExit2() {
      
            this.client && this.client.unpublish(this.localStream);
            this.localStream && this.localStream.close();
            this.client &&
               this.client.leave(
                  () => {
                  console.log("Client succeed to leave.");
                  },
                  () => {
                  console.log("Client failed to leave.");
                  }
               );
            
            this.readyState = false;
            this.client = null;
            this.localStream = null;

            // redirect to index
               if (this.AuthCoach){
                  this.$router.push("/coach/live/summary")
               }else{
                  this.$router.push("/student/live/summary");
               }
           
         },

         handleExit(e) {
            if (e.currentTarget.classList.contains("disabled")) {
               return;
            }
            try {
            this.client && this.client.unpublish(this.localStream);
            this.localStream && this.localStream.close();
            this.client && this.client.leave(
                  () => {
                  console.log("Client succeed to leave.");
                  },
                  () => {
                  console.log("Client failed to leave.");
                  }
               );
            } finally {
               this.readyState = false;
               this.client = null;
               this.localStream = null;
               // redirect to index
               if (this.AuthCoach){
                  this.$router.push("/coach/live/summary")
               }else{
                  this.$router.push("/student/live/summary");
               }
            }
         },
         ScreenSharing(){
            this.ShareScreen = true
            // let CDC = null 

            // if(this.$store.state.BrowserDetect == "isFirefox"){
            //    CDC = "h264"
            // }else{
            //    CDC = "vp8"
            // }

            this.displayMode="share"
            // init AgoraRTC local client
            this.shareClient = AgoraRTC.createClient({ mode: "rtc", codec: 'h264' });
            this.shareClient.init(this.appId, () => {
               console.log("AgoraRTC client initialized");
               this.client.on("stream-added", function(evt) {
                  let stream = evt.stream;
                  
                 
                  console.log("New stream added: " + stream.getId());
                  console.log("New stream added: " + evt.msg)
                  
                  console.log("At " + new Date().toLocaleTimeString());
                  console.log("Subscribe ", stream);
                  this.client.subscribe(stream, function(err) {
                     console.log("Subscribe stream failed", err);
                  });
               });

               this.shareClient.join(this.appId, this.channel,  this.uid ,uid => {
               this.UpdateLayout(uid)
               console.log("User " + uid + " join channel successfully");
               console.log("At " + new Date().toLocaleTimeString());
               
               // create local stream
               // It is not recommended to setState in function addStream
               this.shareStream = this.streamInitShare(uid, this.attendeeMode, '360p_4');
               this.shareStream.init(
                  () => {
                     if (this.attendeeMode !== "audience") {
                        this.addStream(this.shareStream, false);
                        this.shareClient.publish(this.shareStream, err => {
                           console.log("Publish local stream error: " + err);
                        });
                     }
                     this.readyState = true;
                  },
                  err => {
                     console.log("getUserMedia failed", err);
                     this.readyState = true;
                  }
               );
               });
            });
         }

      },
      created() {
         
         // init AgoraRTC local client
         this.client = AgoraRTC.createClient({ mode: "rtc", codec: this.transcode });
         this.client.init(this.appId, () => {
            console.log("AgoraRTC client initialized");
            this.subscribeStreamEvents();
            this.client.join(this.appId, this.channel, this.uid, uid => {
               this.UIDS = uid
               if (this.AuthCoach){
                  this.AuthRecording()
               }
               console.log("User " + uid + " join channel successfully" + this.UIDS); 
               

               console.log("At " + new Date().toLocaleTimeString());
               // create local stream
               // It is not recommended to setState in function addStream
               this.localStream = this.streamInit(uid, this.attendeeMode, this.videoProfile);
               this.localStream.init(
                  () => {
                     if (this.attendeeMode !== "audience") {
                     this.addStream(this.localStream, true);
                     this.client.publish(this.localStream, err => {
                        console.log("Publish local stream error: " + err);
                     });
                     }
                     this.readyState = true;
                  },
                  err => {
                     console.log("getUserMedia failed", err);
                     this.readyState = true;
                  }
               );
            });
         });
         this.datetimestart = this.FormatStartTime + " " + this.GET_DataUpcoming.start_time
         this.datetimeend = this.FormatStartTime + " " + this.GET_DataUpcoming.end_time
      },

      computed: {
   
         GET_DataUpcoming(){
            return this.DataUpcoming[0]
         },
         FormatStartTime () {
            return this.GET_DataUpcoming.date ? moment(this.GET_DataUpcoming.date).format('MMMM D, YYYY') : ''
         },
         CCK_WHO_CAN_BE_HOST(){
            return this.$store.getters.CCK_WHO_CAN_BE_HOST
         },
         CheckNotifwaitung(){
            return this.$store.state.AlertFive
         }
      },

      mounted() {
         this.$nextTick(() => {
            // add listener to control btn group
            let canvas = document.querySelector("#ag-canvas");
            let btnGroup = document.querySelector(".ag-btn-group");
            canvas.addEventListener("mousemove", () => {
               if (global._toolbarToggle) {
                  clearTimeout(global._toolbarToggle);
               }
               btnGroup.classList.add("active");
               global._toolbarToggle = setTimeout(function() {
                  btnGroup.classList.remove("active");
               }, 2000);
      
            });
         
         });

         if(this.$store.state.UserRoleId === 'coach'){
            this.AuthCoach = true
         }else{
             this.AuthCoach = false
         }

         console.log(this.GET_DataUpcoming)

      },

      watch: {
         streamList(val){
            if (val){
                  console.log(val)
               
            }
         },
         CCK_WHO_CAN_BE_HOST(val){
            if (val == true){
               setTimeout(() => {
                  const DataRec = {
                     session : this.$store.state.AgoraInfo.channel,
                     sid :  this.$store.state.StartRecord.data.data.sid,
                     session_type : 'Agora'
                  }
               this.$store.dispatch('POST_DATA_AGORA_REC', DataRec)
               }, 6000);
              
            }else{
               return 0
            }
         },
         CheckNotifwaitung(val){
            if(!val)
            return
            else{
               if (val === '1m'){
                  setTimeout(() => {
                     this.NotifWaiting = false
                  }, 1000);
               }
            }
         }
      },
      beforeUpdate() {
         
         let $ = this;
         // rerendering
         let canvas = document.querySelector("#ag-canvas");
         // pip mode (can only use when less than 4 people in channel)
         if ($.displayMode === "pip") {
            let no = $.streamList.length;
            if (no > 4) {
               $.displayMode = "tile";
               return;
            }
            $.streamList.map((item, index) => {
            let id = item.getId();
            let dom = document.querySelector("#ag-item-" + id);
            if (!dom) {
               dom = document.createElement("section");
               dom.setAttribute("id", "ag-item-" + id);
               dom.setAttribute("class", "ag-item");
               canvas.appendChild(dom);
               item.play("ag-item-" + id);
            }
            if (index === no - 1) {
               //dom.setAttribute("style", `grid-area: span 12/span 24/13/25;`);;
               dom.setAttribute("style", ` grid-area: span 3/span 4/5/25;height:150px;width:150px;z-index:1;`);
               dom.setAttribute("class", "ag-items");
            }else if(index === no - 2){
               // dom.setAttribute(
               //    "style",
               //    `grid-area: span 3/span 4/5/25;
               //    z-index:1;width:150px;height:150px`
               // );
               // dom.setAttribute("class", "ag-items");
               dom.setAttribute("style", `grid-area: span 12/span 24/13/25;`);
            }
            else if(index === no - 3 ) {
               dom.setAttribute("style", `grid-area: span 12/span 24/25/25;min-height:700px;`);
            }
            else if(index === no - 4 ) {
               dom.setAttribute("style", `grid-area: span 12/span 24/38/25;min-height:700px;`);
            }
               item.player.resize && item.player.resize();
            });
         } else if ($.displayMode === "tile") {
            // tile mode
            let no = $.streamList.length;
            $.streamList.map((item, index) => {
            let id = item.getId();
            let dom = document.querySelector("#ag-item-" + id);
            if (!dom) {
               dom = document.createElement("section");
               dom.setAttribute("id", "ag-item-" + id);
               dom.setAttribute("class", "ag-item");
               canvas.appendChild(dom);
               item.play("ag-item-" + id);
            }
            dom.setAttribute("style", `grid-area: ${tile_canvas[no][index]}`);
            item.player.resize && item.player.resize();
            });
         } else if ($.displayMode === "share") {
            
         }
        
      },

      beforeDestroy () {
         this.client && this.client.unpublish(this.localStream);
         this.localStream && this.localStream.close();
         this.client &&
            this.client.leave(
            () => {
               if (this.AuthCoach){
                  this.$store.dispatch('STOP_RECORDING')
               }
         
               console.log("Client succeed to leave.");
            },
            () => {
               console.log("Client failed to leave.");
            }
            );
      }
   }
</script>


<style>
@import '../../../../styles/icons.css';

.bxShareScreen{
   min-height: 550px;
   order:2;
   border:1px solid red;
   
}

#ag-canvas {
  min-height: 600px;
  display: grid;
  grid-gap: 10px;
  align-items: center;
  justify-items: center;
  grid-template-rows: repeat(12, auto);
  grid-template-columns: repeat(24, auto);
  position: relative;
  border:.5px solid #EAEAEA;
 
}

.BX-agcanvas :nth-child(1) {
  order:1;
}

.BX-agcanvas :nth-child(3) {
  order:2;
}
.ag-item {
  /* background: url("../assets/images/avatar.png") center no-repeat; */
  height: 600px;
  width: 100%;
}

#ag-resize-container {
  background-image: none !important;
  background-color: black;
  display: flex;
  justify-content: center;
  height: center;
}

#ag-resize-container .ag-item {
  border: none !important;
}

.bxButton{
   order:4
}

/* button group */
.ag-btn-group {
  height: 50px;
  width: 100%;
  margin: 0 auto;
  background: transparent;
  z-index: 11;
  display: flex;
  justify-content: space-around;
  align-items: center;
  
}

.ag-btn-group:hover,
.ag-btn-group.active {
  background: rgba(18, 63, 99, 0.8);
}

.ag-btn {
  z-index: 12;
  opacity: 0;
  color: white;
  /* width: 35px;
    height: 35px; */
  cursor: pointer;
  font-size: 50px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.ag-btn:hover {
  color: #00b6ed;
}

.ag-btn.disabled,
.ag-btn.disabled:hover {
  color: #637c8e;
  cursor: not-allowed;
  pointer-events: none;
}

.ag-btn-group:hover .ag-btn,
.ag-btn-group.active .ag-btn {
  opacity: 1;
}
.ag-btn.off.videoControlBtn .ag-icon-camera {
  display: none;
}
.ag-btn.videoControlBtn .ag-icon-camera-off {
  display: none;
}
.ag-btn.videoControlBtn .ag-icon-camera {
  display: inline-block;
}
.ag-btn.off.videoControlBtn .ag-icon-camera-off {
  display: inline-block;
}
.ag-btn.off.audioControlBtn .ag-icon-mic {
  display: none;
}
.ag-btn.audioControlBtn .ag-icon-mic-off {
  display: none;
}

.ag-btn.audioControlBtn .ag-icon-mic {
  display: inline-block;
}
.ag-btn.off.audioControlBtn .ag-icon-mic-off {
  display: inline-block;
}

video {
  max-width: unset !important;
}
</style>


