<template>
	<div class="ContVideoLive">
		<div class="Bx-CountAlert">
         <div class="BxCountDownt">
            <div class="countdowns">
               <Countdown
                	v-on:event_child="handleExitFromCountdown"
               	:startTime = datetimestart
                	:endTime= datetimeend
                	trans='{
                    "day":"Day",
                    "hours":"Hours",
                    "minutes":"Minuets",
                    "seconds":"Seconds",
                    "expired":"Event has been expired.",
                    "running":"Till the end of event.",
                    "upcoming":"Till start of event.",
                    "status": {
                        "expired":"Session Expired",
                        "running":"Session is Running",
                        "upcoming":"Upcoming Session"
                    }
                }'  
               />
            </div>
         </div>
      </div>
		<v-snackbar
         v-model="NotifWaiting"
         color="blue darken-3"
         :timeout="timeout"
         top center
         style="margin:auto;"
         class="SesNotif"
         v-if="this.streamList.length <= 1"
         >
            <p v-if="!authCoach"  style="margin-bottom:0px;text-align:center;width: 100%;font-style:italic;font-size:.8em;">Waiting for Coach to Join the Session, Please remain in session until the end in order to receive a refund of your tokens</p>

            <p v-if="authCoach" style="margin-bottom:0px;text-align:center;width: 100%;font-style:italic;font-size:.8em;">Waiting for Student to Join the Session, Please remain in session until the end to receive tokens.</p>
			</v-snackbar>
		<div id="ag-canvas">
			<span 
				v-if="!authCoach && !fullShareScreenStatus && streamList.length == 3"
				@click="FullShareScreen" 
				class="BtnMaxShareScreen"
				title="Full Screen Share"
			>
				<i class="fas fa-expand-arrows-alt fa-2x"></i>
			</span>
			<span 
				v-if="!authCoach && fullShareScreenStatus"
				@click="RemoveFullShareScreen"  
				class="BtnMaxShareScreen"
				title="Remove Full Screen Share"
			>
				<i class="fas fa-compress-arrows-alt fa-2x"></i>
			</span>
			<div class="ag-btn-group">
				<!-- <span 
					@click="handleExit"
					class="ag-btn exitBtn"
					:class="{'disabled': !readyState}"
					title="Exit">
					<i class="ag-icon ag-icon-leave"></i>
				</span> -->
				<span v-if="attendeeMode === 'video'"
					@click="handleCamera"
					class="ag-btn videoControlBtn" 
					title="Enable/Disable Video">
					<i class="ag-icon ag-icon-camera"></i>
					<i class="ag-icon ag-icon-camera-off"></i>
				</span>
				<span v-if="attendeeMode !== 'audience'"
					@click="handleMic"
					class="ag-btn audioControlBtn" 
					title="Enable/Disable Audio">
					<i class="ag-icon ag-icon-mic"></i>
					<i class="ag-icon ag-icon-mic-off"></i>
				</span>
				<span 
					@click="switchDisplay"
					class="ag-btn displayModeBtn"
					:class="{'disabled': streamList.length === 1 || streamList.length === 3 || displayMode === 'share'}"
					title="Switch Display Mode">
					<i class="ag-icon ag-icon-switch-display"></i>
				</span>
				<span 
					class= "ag-btn disableRemoteBtn"
					:class="{'disabled': streamList.length === 1 || streamList.length === 3 || displayMode !== 'pip'}"
					@click="hideRemote"
					title="Hide Remote Stream">
					<i class="ag-icon ag-icon-remove-pip"></i>
				</span>
				<span 
					v-if="!shareScreenStatus && authCoach "
					@click= "screenShare"
					class= "ag-btn screenShare"
					:class="{'disabled': streamList.length === 1 || streamList.length === 3 || fullScreenStatus}"
					title="Screen Share">
					<i class="ag-icon ag-icon-screen-share"></i>
				</span>
				<span 
					v-if="shareScreenStatus && authCoach"
					@click = "DisablescreenShare"
					class= "ag-btn disablescreenShare"
					title="Disable Screen Share">
					<i  class="ag-icon ag-icon-invalid"></i>
				</span>
				<span
					v-if="!fullScreenStatus"
					@click="FullScreenAG"
					:class="{'disabled': streamList.length == 1 || streamList.length == 3 || displayMode === 'share'}"
					class= "ag-btn FullscreenIcon"
					title="Full Screen">
					<i class="fa fa-expand"></i>
				</span>
				<span
					v-if="fullScreenStatus"
					@click="RemoveFullScreenAg"
					class= "ag-btn RemoveFullscreenIcon"
					title="Remove Full Screen">
					<i class="fa fa-compress"></i>
				</span>
			</div>
  		</div>
	</div>
  
</template>

<script>
import { merge } from "lodash";
import AgoraRTC from 'agora-rtc-sdk';
import Countdown from './countdown/Countdown.vue';
import moment from 'moment';

const tile_canvas = {
	"1": ["span 12/span 24"],
	"2": ["span 12/span 12/13/25", "span 12/span 12/13/13"],
	"3": ["span 6/span 12", "span 6/span 12", "span 6/span 12/7/19"],
	"4": [
		"span 6/span 12",
		"span 6/span 12",
		"span 6/span 12",
		"span 6/span 12/7/13"
	],
	"5": [
		"span 3/span 4/13/9",
		"span 3/span 4/13/13",
		"span 3/span 4/13/17",
		"span 3/span 4/13/21",
		"span 9/span 16/10/21"
	],
	"6": [
		"span 3/span 4/13/7",
		"span 3/span 4/13/11",
		"span 3/span 4/13/15",
		"span 3/span 4/13/19",
		"span 3/span 4/13/23",
		"span 9/span 16/10/21"
	],
	"7": [
		"span 3/span 4/13/5",
		"span 3/span 4/13/9",
		"span 3/span 4/13/13",
		"span 3/span 4/13/17",
		"span 3/span 4/13/21",
		"span 3/span 4/13/25",
		"span 9/span 16/10/21"
	]
};

export default {
	data() {
		return {
			client: {},
			localStream: {},
			shareClient: {},
			shareStream: {},
			displayMode: "pip",
			streamList: [],
			readyState: false,
			shareScreenStatus: false,
			fullScreenStatus : false,
			fullShareScreenStatus : false,
			isIdSHareScreen: null,

			//countdown
			datetimestarts:'08 november, 2019 15:00:00',
			datetimeends: '08 november, 2019 15:50:00',
			datetimestart:'',
         datetimeend: '',
			//AlertWaiting
			NotifWaiting: true,
         timeout: 6000000,

		};
	},

	props: [
		"transcode",
		"attendeeMode",
		"videoProfile",
		"channel",
		"baseMode",
		"appId",
		"uid",
		"authCoach",
		"DataUpcoming"
	],

	components:{
      Countdown
	},

	created() {
		let $ = this;
		$.datetimestart = $.FormatStartTime + " " + $.GET_DataUpcoming.start_time
      $.datetimeend = $.FormatStartTime + " " + $.GET_DataUpcoming.end_time
    // init AgoraRTC local client
    	$.client = AgoraRTC.createClient({ mode:"rtc", codec: $.transcode});
    	$.client.init($.appId, () => {
      	console.log("AgoraRTC client initialized");
      	$.subscribeStreamEvents();
      	$.client.join($.appId, $.channel, $.uid, uid => {
				console.log("User " + uid + " join channel successfully");
				console.log("At " + new Date().toLocaleTimeString());
       		// create local stream
        		// It is not recommended to setState in function addStream
        		$.localStream = this.streamInit(uid, $.attendeeMode, $.videoProfile);
        		$.localStream.init(
				() => {
					if ($.attendeeMode !== "audience") {
					$.addStream($.localStream, true);
					$.client.publish($.localStream, err => {
						console.log("Publish local stream error: " + err);
					});
					}
					$.readyState = true;
				},
				err => {
					console.log("getUserMedia failed", err);
					$.readyState = true;
				}
				);
      	});
		});

	},

	mounted() {
    	this.$nextTick(() => {
      // add listener to control btn group
			let canvas = document.querySelector("#ag-canvas");
			let btnGroup = document.querySelector(".ag-btn-group");
			
			canvas.addEventListener("mousemove", () => {
				if (global._toolbarToggle) {
					clearTimeout(global._toolbarToggle);
				}
				btnGroup.classList.add("active");
				
				global._toolbarToggle = setTimeout(function() {
					btnGroup.classList.remove("active");
					
				}, 2000);
			});
		});

		var constraints = {
			video: false,
			audio: false,
			screen: false
		}

  	},

  	methods: {
		streamInit(uid, attendeeMode, videoProfile, config) {
			let defaultConfig = {
				streamID: uid,
				audio: true,
				video: true
			};

			switch (attendeeMode) {
			case "audio-only":
				defaultConfig.video = false;
				break;
			case "audience":
				defaultConfig.video = false;
				defaultConfig.audio = false;
				break;
			default:
			case "video":
				break;
			}

			let stream = AgoraRTC.createStream(merge(defaultConfig, config));
			stream.setVideoProfile(videoProfile);
			return stream;
		},

		streamInitShare(uid, attendeeMode, videoProfile, config) {
            let defaultConfig = {
               streamID: 'SHARES',
               audio: false,
               video: false,
               screen: true,
               // extensionId: 'minllpmhdgpndnkomcoccfekfegnlikg',
               mediaSource: 'window'
            };

            let stream = AgoraRTC.createStream(merge(defaultConfig, config));
            stream.setVideoProfile(videoProfile);
            return stream;
			},

		subscribeStreamEvents() {
			let rt = this;
			rt.client.on("stream-added", function(evt) {
				let stream = evt.stream;
				console.log("New stream added: " + stream.getId());
				console.log("At " + new Date().toLocaleTimeString());
				console.log("Subscribe ", stream);
				rt.client.subscribe(stream, function(err) {
					console.log("Subscribe stream failed", err);
				});
			});

			rt.client.on("peer-leave", function(evt) {
				console.log("Peer has left: " + evt.uid);
				console.log(new Date().toLocaleTimeString());
				console.log(evt);
				rt.removeStream(evt.uid);
			});

			rt.client.on("stream-subscribed", function(evt) {
				let stream = evt.stream;
				console.log("Got stream-subscribed event");
				console.log(new Date().toLocaleTimeString());
				console.log("Subscribe remote stream successfully: " + stream.getId());
				console.log(evt);
				rt.addStream(stream);
			});

			rt.client.on("stream-removed", function(evt) {
				let stream = evt.stream;
				console.log("Stream removed: " + stream.getId());
				console.log(new Date().toLocaleTimeString());
				console.log(evt);
				rt.removeStream(stream.getId(), stream.profile);
			});

		},

		removeStream(uid) {
			this.streamList.map((item, index) => {
				if (item.getId() === uid && item.profile !== "480p_2") {
					item.close();
					let element = document.querySelector("#ag-item-" + uid);
					if (element) {
						element.parentNode.removeChild(element);
					}
					let tempList = [...this.streamList];
					tempList.splice(index, 1);
					this.streamList = tempList;
				}else if(item.getId() === uid && item.profile === "480p_2"){
					item.close();
					let element = document.querySelector("#ag-item-" + uid);
					if (element) {
						element.parentNode.removeChild(element);
					}
					let tempList = [...this.streamList];
					tempList.splice(index, 1);
					this.streamList = tempList;

					this.DisablescreenShare()
					this.displayMode="pip"
				}


				if(!this.authCoach){
					if (item.getId() === this.isIdSHareScreen){
						this.isIdSHareScreen = null
						this.fullShareScreenStatus = false
						console.log(this.isIdSHareScreen)
						console.log('isIdSHareScreen CLEAR')
					}
				}

			});
		},

		addStream(stream, push = false) {
			let repeatition = this.streamList.some(item => {
				return item.getId() === stream.getId();
			});
			if (repeatition) {
				return;
			}
			if (push) {
				this.streamList = this.streamList.concat([stream]);
			} else {
				this.streamList = [stream].concat(this.streamList);
			}
		},

		handleCamera(e) {
			e.currentTarget.classList.toggle("off");
			this.localStream.isVideoOn()
			? this.localStream.muteVideo()
			: this.localStream.unmuteVideo();
		},

		handleMic(e) {
			e.currentTarget.classList.toggle("off");
			this.localStream.isAudioOn()
			? this.localStream.muteAudio()
			: this.localStream.unmuteAudio();
		},


		screenShare(e){
			this.displayMode="share"
            // init AgoraRTC local client
			this.shareClient = AgoraRTC.createClient({ mode: "rtc", codec: 'h264' });
			this.shareClient.init(this.appId, () => {
				console.log("AgoraRTC client initialized");
				this.client.on("stream-added", function(evt) {
					let stream = evt.stream;
					
					// console.log("New stream added: " + stream.getId());
					// console.log("New stream added: " + evt.msg)
				
					// console.log("At " + new Date().toLocaleTimeString());
					// console.log("Subscribe ", stream);
					this.client.subscribe(stream, function(err) {
						console.log("Subscribe stream failed", err);
					});
					

				});

				this.shareClient.join(this.appId, this.channel,  this.uid ,uid => {
				
					console.log("User " + uid + " join channel successfully");
					console.log("At " + new Date().toLocaleTimeString());
					
					// create local stream
					// It is not recommended to setState in function addStream
					this.shareStream = this.streamInitShare(uid, this.attendeeMode, '480p_2');
					this.shareStream.init(
						() => {
							if (this.attendeeMode !== "audience") {
								this.addStream(this.shareStream, true);
								this.shareClient.publish(this.shareStream, err => {
									console.log("Publish local stream error: " + err);
								});
								this.shareScreenStatus = true
							}
							this.readyState = true;
						},
						err => {
							console.log("getUserMedia failed", err);
							this.shareScreenStatus = false
							this.displayMode = "pip"
							this.readyState = true;
						}
					);
				});
				console.log(this.client)
			});

		},
		DisablescreenShare(e){
			this.shareClient && this.shareClient.unpublish(this.shareStream);
         this.shareStream && this.shareStream.close();
         this.shareClient && this.shareClient.leave(
				() => {
				console.log("Share Screen Success Remove.");
				this.shareScreenStatus = false
				},
				() => {
				console.log("Share Screen failed to Remove.");
				}
         );
		},
		switchDisplay(e) {
			if (
				e.currentTarget.classList.contains("disabled") ||
				this.streamList.length <= 1
			) {
				return;
			}
			if (this.displayMode === "pip") {
				this.displayMode = "tile";
			} else if (this.displayMode === "tile") {
				this.displayMode = "pip";
			} else if (this.displayMode === "share") {
				this.displayMode = "pip";
			} else {
				console.error("Display Mode can only be tile/pip/share");
			}
		},

		hideRemote(e) {
			if (
				e.currentTarget.classList.contains("disabled") ||
				this.streamList.length <= 1
			) {
				return;
			}
			let list;
			let id = this.streamList[this.streamList.length - 2].getId();
			list = Array.from(
				document.querySelectorAll(`.ag-item:not(#ag-item-${id})`)
			);
			list.map(item => {
				if (item.style.display !== "none") {
					item.style.display = "none";
				} else {
					item.style.display = "block";
				}
			});
		},

    	handleExit(e) {
			if (e.currentTarget.classList.contains("disabled")) {
				return;
			}
			try {
				this.client && this.client.unpublish(this.localStream);
				this.localStream && this.localStream.close();
				this.client &&
				this.client.leave(
					() => {
					console.log("Client succeed to leave.");
					},
					() => {
					console.log("Client failed to leave.");
					}
				);
			} finally {
				this.readyState = false;
				this.client = null;
				this.localStream = null;
			// redirect to index
				this.$router.push("/");
			}
		},

		handleExitFromCountdown() {
			this.client && this.client.unpublish(this.localStream);
         this.localStream && this.localStream.close();
         this.client &&
				this.client.leave(
					() => {
					console.log("Client succeed to leave.");
					},
					() => {
					console.log("Client failed to leave.");
					}
				);
            
         this.readyState = false;
         this.client = null;
         this.localStream = null;

         // redirect to index
         if (this.authCoach){
            this.$router.push("/coach/live/summary")
         }else{
            this.$router.push("/student/live/summary");
         }
		},

		FullScreenAG(){
			this.displayMode = "pip"

			let canvas = document.querySelector("#ag-canvas");
			canvas.classList.add("FullScreen");

			let canvasCountdown = document.querySelector(".Bx-CountAlert");
			canvasCountdown.classList.add("CountdownFullScreen");

			
			this.fullScreenStatus = true
		},

		RemoveFullScreenAg(){
			let canvas = document.querySelector("#ag-canvas");
			canvas.classList.remove("FullScreen");

			let canvasCountdown = document.querySelector(".Bx-CountAlert");
			canvasCountdown.classList.remove("CountdownFullScreen");

			this.fullScreenStatus = false
		},

		FullShareScreen(){
			this.$nextTick(() => {
				let canvasButton = document.querySelector(".BtnMaxShareScreen");
				canvasButton.classList.add("IsActive");
				let canvasCountdown = document.querySelector(".Bx-CountAlert");
				canvasCountdown.classList.add("CountdownFullScreen");

				setTimeout(() => {
					let canvasShare = document.querySelector("#ag-item-"+this.isIdSHareScreen);
					canvasShare.classList.add("MaxShare");
					let VideoShare = document.querySelector("#video"+this.isIdSHareScreen);
					VideoShare.classList.add("VideoReset");
				}, 500);
				this.fullShareScreenStatus = true

			})
			
		},


		RemoveFullShareScreen(){
			let canvas = document.querySelector("#ag-item-"+this.isIdSHareScreen);
			canvas.classList.remove("MaxShare");

			let canvasCountdown = document.querySelector(".Bx-CountAlert");
			canvasCountdown.classList.remove("CountdownFullScreen");

			let canvasButton = document.querySelector(".BtnMaxShareScreen");
			canvasButton.classList.remove("IsActive");

			let VideoShare = document.querySelector("#video"+this.isIdSHareScreen);
			VideoShare.classList.remove("VideoReset");


			this.fullShareScreenStatus = false
		}

	},
	  
	computed: {
		CheckNotifwaitung(){
         return this.$store.state.AlertFive
		},
		GET_DataUpcoming(){
         return this.DataUpcoming[0]
		},
		FormatStartTime () {
			return this.GET_DataUpcoming.date ? moment(this.GET_DataUpcoming.date).format('MMMM D, YYYY') : ''
      },
	},

	watch: {
		isIdSHareScreen(val){
			if (val !== null ){
				console.log(val)
				console.log('FUCK')
			}
		},
		CheckNotifwaitung(val){
         if(!val)
            return
            else{
               if (val === '1m'){
                  setTimeout(() => {
                     this.NotifWaiting = false
                  }, 1000);
               }
            }
      }
	},
  	

  	beforeUpdate() {
    	let $ = this;
    // rerendering
    	let canvas = document.querySelector("#ag-canvas");
    // pip mode (can only use when less than 4 people in channel)
    	if ($.displayMode === "pip") {
      	let no = $.streamList.length;
      	if (no > 4) {
        		$.displayMode = "tile";
        		return;
      	}
      	$.streamList.map((item, index) => {
        		let id = item.getId();
        		let dom = document.querySelector("#ag-item-" + id);
        		if (!dom) {
					dom = document.createElement("section");
					dom.setAttribute("id", "ag-item-" + id);
					dom.setAttribute("class", "ag-item");
					canvas.appendChild(dom);
					item.play("ag-item-" + id);
				}
				if (index === no - 1) {
					dom.setAttribute("style", ` grid-area: span 3/span 4/5/25;height:150px;width:146px;z-index:1;border: 1px #00b6ed solid;`);
					// dom.setAttribute("class", "ag-items");
				}else if(index === no - 2){
					dom.setAttribute("style", `grid-area: span 12/span 24/13/25;min-height:600px;`);
				}else if(index === no - 3 ) {
					dom.setAttribute("style", `grid-area: span 12/span 24/25/25;min-height:700px;`);
					dom.setAttribute("class", "ag-item");
					if(!this.authCoach){
						this.isIdSHareScreen = id
						console.log(this.isIdSHareScreen)
						console.log('fuck')

					}
				}else if(index === no - 4 ) {
					dom.setAttribute("style", `grid-area: span 12/span 24/38/25;min-height:700px;`);
				}
				item.player.resize && item.player.resize();
			});
    	} else if ($.displayMode === "tile") {
      // tile mode
      	let no = $.streamList.length;
      	$.streamList.map((item, index) => {
			let id = item.getId();
			let dom = document.querySelector("#ag-item-" + id);
			if (!dom) {
				dom = document.createElement("section");
				dom.setAttribute("id", "ag-item-" + id);
				dom.setAttribute("class", "ag-item");
				canvas.appendChild(dom);
				item.play("ag-item-" + id);
			}
				dom.setAttribute("style", `grid-area: ${tile_canvas[no][index]}`);
				item.player.resize && item.player.resize();
			});
    	} else if ($.displayMode === "share") {
      // screen share mode (tbd)
    	}
  	},

	beforeDestroy () {
		this.client && this.client.unpublish(this.localStream);
		this.localStream && this.localStream.close();
		this.client &&
			this.client.leave(
			() => {
				console.log("Client succeed to leave.");
			},
			() => {
				console.log("Client failed to leave.");
			});
	},
	
};
</script>

<style>
@import '../../../../styles/icons.css';
.bxShareScreen{
   min-height: 550px;
   order:2;
   border:1px solid red;
   
}
#ag-canvas {
  min-height: 600px;
  display: grid;
  grid-gap:  0px;
  align-items: center;
  justify-items: center;
  grid-template-rows: repeat(12, auto);
  grid-template-columns: repeat(24, auto);
  position: relative;
}

.BX-agcanvas :nth-child(1) {
  order:1;
}

.BX-agcanvas :nth-child(3) {
  order:2;
}
.ag-item {
  border-radius: 0px;
  height: 100%;
  width: 100%;
}

#ag-resize-container {
  background-image: none !important;
  background-color: black;
  display: flex;
  justify-content: center;
  height: center;
}

#ag-resize-container .ag-item {
  border: none !important;
}

/* button group */
.ag-btn-group {
  height: 50px;
  width: 100%;
  margin: 0 auto;
  position: absolute;
  bottom: 0;
  background: transparent;
  z-index: 11;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.FullScreen{
	position : fixed !important;
	top:0;
	left:0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	z-index: 102;
}

.CountdownFullScreen{
	position : fixed !important;
	top:0 !important;
	left:0 !important;
	z-index: 103 !important;
}

.MaxShare{
	position : fixed !important;
	top:0;
	left:0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	z-index: 102;
}

.BtnMaxShareScreen{
	position: absolute;
	bottom: 60px;
	right: 20px;
	cursor: pointer;
	opacity: 1;
	z-index: 101;
}

.IsActive{
	position : fixed !important;
	z-index: 105 !important;
}

.VideoReset{
	object-fit: contain !important;
}

.fa-expand-arrows-alt, .fa-compress-arrows-alt{
	color: #00b6ed70;
}

.fa-expand-arrows-alt:hover, .fa-compress-arrows-alt:hover{
	color: #00b6ed;
}


.ag-btn-group:hover,
.ag-btn-group.active {
  background: rgba(18, 74, 99, 0.8);
}

.ag-btn {
  z-index: 12;
  opacity: 0;
  color: white;
   width: 5%;
	height: 100%;
  cursor: pointer;
  font-size: 43px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.ag-btn:hover {
  color: #00b6ed;
}

.ag-btn.disabled,
.ag-btn.disabled:hover {
  color: #637c8e;
  cursor: not-allowed;
}

.ag-btn-group:hover .ag-btn,
.ag-btn-group.active .ag-btn {
  opacity: 1;
}
.ag-btn.off.videoControlBtn .ag-icon-camera {
  display: none;
}
.ag-btn.videoControlBtn .ag-icon-camera-off {
  display: none;
}
.ag-btn.videoControlBtn .ag-icon-camera {
  display: inline-block;
}
.ag-btn.off.videoControlBtn .ag-icon-camera-off {
  display: inline-block;
}

.ag-btn.off.audioControlBtn .ag-icon-mic {
  display: none;
}
.ag-btn.audioControlBtn .ag-icon-mic-off {
  display: none;
}
.ag-btn.audioControlBtn .ag-icon-mic {
  display: inline-flex;
}
.ag-btn.off.audioControlBtn .ag-icon-mic-off {
  display: inline-flex;
}

.ag-btn.off.screenShare .ag-icon-screen-share {
  display: none;
}
.ag-btn.screenShare .ag-icon-screen-share {
  
}
.ag-btn.off.screenShare .ag-icon-invalid {
  display: inline-block;
}
.ag-btn.screenShare .ag-icon-invalid {
  display: inline-block;
  font-size:40px !important;
}

.ag-btn.FullscreenIcon{
	height: 100%;
   width: 5%;
}
.ag-btn.FullscreenIcon  .fa.fa-expand, .fa-expand-arrows-alt{
  display: inline-block;
  font-size: 28px !important;
  margin-top: 2px;
}

.ag-btn.RemoveFullscreenIcon{
	height: 100%;
   width: 5%;
}
.ag-btn.RemoveFullscreenIcon  .fa.fa-compress, .fa-compress-arrows-alt{
  display: inline-block;
  font-size: 28px !important;
  margin-top: 2px;
}









video {
  max-width: unset !important;
}
</style>




