<template>
   <div class="BXliveSession__BxGroup__BXvideoFrame">
      <div class="BXliveSession__BxGroup__BXvideoFrame--bxVideoLive ag-main">
        <!-- <AgoraVC 
            :videoProfile="videoProfile"
            :channel="channel"
            :transcode="transcode"
            :attendeeMode="attendeeMode"
            :baseMode="baseMode"
            :appId="appId"
            :uid="uid"
            :UidRecord="UidRecord"
            :DataUpcoming = 'DataUpcoming'
        />  -->
        <AgoraVC2
            :videoProfile="videoProfile"
            :channel="channel"
            :transcode="transcode"
            :attendeeMode="attendeeMode"
            :baseMode="baseMode"
            :appId="appId"
            :uid ="uid"
            :authCoach="authCoach"
            :DataUpcoming = 'DataUpcoming'
        /> 
      </div>
   </div>
</template>

<script>
   import AgoraRTC from 'agora-rtc-sdk'
   import {AGORA_APP_ID} from '../../AgoraConfig/agoraConfig.js'
   import AgoraVC from './AgoraVideoCall/AgoraVideoCall.vue'
   import AgoraVC2 from './AgoraVideoCall/AgoraVideoCall2.vue'
   export default {
      components:{
         AgoraVC,
         AgoraVC2
      },
      props: [
         'DataUpcoming'
      ],
      data() {
         return {
            videoProfile: "360p_4",
            channel: "",
            transcode: "", 
            attendeeMode: "video",
            baseMode:  "al",
            uid: undefined,
            UidRecord: "",
            authCoach: false
         }
      },
      created() {
         this.appId = AGORA_APP_ID
         this.channel = this.$store.state.StartLiveSession.session
         this.UidRecord = this.$store.state.StartLiveSession.id
         this.transcode = this.$store.state.BrowserDetect.Codec
      },
      mounted() {
         if(this.$store.state.UserRoleId === 'coach'){
            this.authCoach = true
         }else{
            this.authCoach = false
         }
      },
   }
</script>

<style lang="css">
   @import '../../../styles/icons.css';

   video {
  max-width: unset !important;
}
#ag-resize-container {
  background-image: none !important;
  background-color: black;
  display: flex;
  justify-content: center;
  height: center;
}

#ag-resize-container .ag-item {
  border: none !important;
}
</style>



