<template>
   <div class="BXliveSession__BXtabMenu">
      <div class="BoxTabLable" v-if="AuthCoach">
          <!-- :class="{spanIsActive:index == current, spanChat:index == 0 && current == 0}" -->
         <span
            v-for="(n, index) in LabelTab" :key="index"
            :class="{spanIsActive:index == current, spanChat:index == ChatNotif}"
            @click="current = index"
         >
            {{n}}
         </span>
      </div>

       <div class="BoxTabLable BoxTabLableStu" v-if="!AuthCoach">
         <span
            v-for="(n, index) in LabelTab2" :key="index"
            :class="{spanIsActive:index == current, spanChat:index == ChatNotif}"
            @click="current = index"
         >
            {{n}}
         </span>
      </div>

      <div class="BoxTabContent">
         <div v-show="current === 0" class="BXscripts">
            <AgoraSignaling
               :GET_DataUpcoming ="GET_DataUpcoming"
               :AuthCoach = "AuthCoach"
            />
         </div>
         <div v-show="current === 1 && AuthCoach" class="BXscripts">
            <CoachNote :DataUpcoming="DataUpcoming" />
         </div>
         <div v-show="current === 2  && AuthCoach" class="BXscripts">
            <content-loader
               :animate="true"
               :height="400"
               :width="300"
               :speed="2"
               primaryColor="#f3f3f3"
               secondaryColor="#ecebeb"
               v-if="myData == null"
            >
               <rect x="20" y="35" rx="2" ry="2" width="260" height="30" />
               <rect x="20" y="75" rx="2" ry="2" width="260" height="30" />
               <rect x="20" y="115" rx="2" ry="2" width="260" height="30"/>
               <rect x="20" y="155" rx="2" ry="2" width="260" height="30" />
               <rect x="20" y="195" rx="2" ry="2" width="260" height="30" />
               <rect x="20" y="235" rx="2" ry="2" width="260" height="30" />
               <rect x="20" y="275" rx="2" ry="2" width="260" height="30" />
               <rect x="20" y="315" rx="2" ry="2" width="260" height="30" />
               <rect x="20" y="355" rx="2" ry="2" width="260" height="30" />
            </content-loader>

            <TabScript
               :StudentId ='this.DataUpcoming[0].username'
               v-if="myData !== null && (this.DataUpcoming[0].b2student == 'b2i' || this.DataUpcoming[0].b2student == null)"
            />

            <TabScriptB2C
               :StudentId ='this.DataUpcoming[0].username'
               v-if="myData !== null && (this.DataUpcoming[0].b2student == 'b2c')"
            />

         </div>
         <div v-show="current === 3 && AuthCoach || current === 1 && !AuthCoach" class="BXscripts">
            <div class="BoxTabLables">
               <div class="BxStudyDat" v-if="this.DataUpcoming[0].b2student == 'b2i' || this.DataUpcoming[0].b2student == null">
                  <span
                     v-for="(n, index) in LabelTab3" :key="index"
                     :class="{subspanIsActive:index == currentData}"
                     @click="currentData = index"
                  >
                     {{n}}
                  </span>
               </div>
               <div class="BoxTabsubContent" v-if="this.DataUpcoming[0].b2student == 'b2i' || this.DataUpcoming[0].b2student == null">
                  <div v-if="currentData === 0" class="BXscripts">
                     <StudyLevel/>
                     <ProgressBar/>
                  </div>
                  <!-- <div v-if="currentData === 1" class="BXscripts">
                     <CertificatePlan/>
                  </div> -->
               </div>

               <div class="BoxTabsubContent" v-if="this.DataUpcoming[0].b2student == 'b2c'">
                  <div class="study__bottom live--sess">
                     <CertCompletion />
                     <PointsGoal :getWeeklyProgress="getWeeklyProgress" />
                     <ProgressGoal />
                     <ComprehensionGoal :getWeeklyProgress="getWeeklyProgress" />
                     <PronunciationGoal :getWeeklyProgress="getWeeklyProgress" />
                     <ListeningGoal :getWeeklyProgress="getWeeklyProgress" />
                     <SpeakingGoal :getWeeklyProgress="getWeeklyProgress" />
                  </div>
               </div>

            </div>
         </div>

      </div>
   </div>
</template>

<script>
   import { ContentLoader } from "vue-content-loader"
   import CoachNote from './TabCoachNote/CoachNote.vue'
   import AgoraSignaling from './AgoraSignaling/AgoraSignaling.vue'
   import TabScript from './TabScript/TabScript.vue'
   import TabScriptB2C from './TabScript/TabScriptB2C'
   import StudyLevel from '../../../student-components/studentProgressReport/StudyLevel.vue'
   import ProgressBar from '../../../student-components/studentProgressReport/ProgressBar.vue'
   import CertificatePlan from '../../../student-components/studentProgressReport/CertificatePlan.vue'
   import CertCompletion from '../../../student-components/study-progress-report/cert-completion/CertCompletionWrapper'
   import ProgressGoal from '../../../student-components/study-progress-report/progress-goal/ProgressGoalWrapper'
   import PointsGoal from '../../../student-components/study-progress-report/bar-chart-point/PointsGoal'
   import ComprehensionGoal from '../../../student-components/study-progress-report/bar-chart-point/ComprehensionGoal'
   import PronunciationGoal from '../../../student-components/study-progress-report/bar-chart-point/PronunciationGoal'
   import ListeningGoal from '../../../student-components/study-progress-report/bar-chart-point/ListeningGoal'
   import SpeakingGoal from '../../../student-components/study-progress-report/bar-chart-point/SpeakingGoal'
   export default {
      components:{
         CoachNote,
         AgoraSignaling,
         TabScript,
         TabScriptB2C,
         StudyLevel,
         ProgressBar,
         CertificatePlan,
         CertCompletion,
         ProgressGoal,
         PointsGoal,
         ComprehensionGoal,
         PronunciationGoal,
         ListeningGoal,
         SpeakingGoal,
         ContentLoader
      },
      props:[
         'DataUpcoming',
         'ResizeScreen'
      ],
      data() {
         return {
            current:0,
            currentData:0,
            currentStue:0,
            LabelTab:['CHAT', 'COACH NOTE', 'SCRIPTS','STUDY DATA'],
            LabelTab2:['CHAT','STUDY DATA'],
            // LabelTab3:['Study Level', 'Academic Plan'],
            LabelTab3:['Study Level'],
            AuthCoach: false,
            Accounts : null,
            myData :null,
            ChatNotif:null
         }
      },
      created() {
         const username = this.DataUpcoming[0].username

         if(this.$store.state.UserRoleId === 'coach'){
            const studentUsername = {
               username: this.DataUpcoming[0].username
            }
            this.AuthCoach = true
            if(this.DataUpcoming[0].b2student == 'b2i' || this.DataUpcoming[0].b2student == null){
               this.$store.dispatch('GET_CALL1_CALL2', studentUsername)
               this.$store.dispatch('GET_DATA_B2I_SCRIPTS', username)
               this.$store.dispatch('GET_MARK_DATA_B2ISCRIPTS', username)
            }else if(this.DataUpcoming[0].b2student == 'b2c'){
               this.$store.dispatch('GET_STUDY_DASHBOARD', username)
               this.$store.dispatch('GET_WEEKLY_PROGRESS', username)
               this.$store.dispatch('GET_DATA_B2C_SCRIPTS', username)
               this.$store.dispatch('GET_MARK_DATA_B2C_SCRIPTS', username)
            }

         } else if (this.$store.state.UserRoleId === 'student'){
            this.AuthCoach = false
            const studentUsername = {
               username: this.$store.state.DataAdditional.username
            }
            this.$store.dispatch('GET_CALL1_CALL2', studentUsername)

         }

      },
      computed: {
         GET_DataUpcoming(){
            return this.DataUpcoming[0]
         },
         getWeeklyProgress () {
            const data = this.$store.state.stateWeeklyProgress
            return data
         },
         CheckLoadTabScript (){
            if (this.$store.getters.CCK_TAB_SCRIPT !== 'IS NULL'){
               const ItemsCont = []
               for (var i in this.$store.getters.CCK_TAB_SCRIPT){
                  ItemsCont.push(this.$store.getters.CCK_TAB_SCRIPT[i])
               }
               return ItemsCont
            }else{
               return 'IS NULL'
            }
         },
         CheckNewChat(){
            return this.$store.state.CckLengthChat
         }
      },
      watch: {
         CheckNewChat(val){
            if(!val)
               return
            else{
               if(this.current !== 0){
                  this.ChatNotif = 0
               }
            }
         },
         current(val){
            if(val == 0 ){
               this.ChatNotif = null
            }
            
            if (!val)
               return
            else if(this.$store.state.UserRoleId === 'coach' && val === 2){

               if(this.DataUpcoming[0].b2student == 'b2i' || this.DataUpcoming[0].b2student == null){

                  if (this.CheckLoadTabScript !== 'IS NULL'){
                     console.log(this.CheckLoadTabScript)
                     const datab2iscript = this.CheckLoadTabScript[0]
                     const dataMarkScript = this.CheckLoadTabScript[1]

                     if(dataMarkScript == 200 && dataMarkScript == 200){
                        setTimeout(() => {
                           this.myData = 'Success'
                        }, 2000);

                     }else if ( dataMarkScript == 400 || dataMarkScript == 400){
                        console.log('tab script 400')
                     }else if ( dataMarkScript == 500 || dataMarkScript == 500){
                        console.log('tab script 500')
                     }
                  }
               }else if(this.DataUpcoming[0].b2student == 'b2c'){

                  if (this.CheckLoadTabScript !== 'IS NULL'){
                     console.log(this.CheckLoadTabScript)
                     const datab2iscript = this.CheckLoadTabScript[0]
                     const dataMarkScript = this.CheckLoadTabScript[1]

                     if(dataMarkScript == 200 && dataMarkScript == 200){
                        setTimeout(() => {
                           this.myData = 'Success'
                        }, 2000);

                     }else if ( dataMarkScript == 400 || dataMarkScript == 400){
                        console.log('tab script 400')
                     }else if ( dataMarkScript == 500 || dataMarkScript == 500){
                        console.log('tab script 500')
                     }
                  }
               }
            }
         }
      },
   }
</script>


<style lang="scss">
   @import '../../../views/studentProgressReport/StudentProgressReport.scss';
  @import '../../../views/study-progress-report/StudyProgressReport.scss';
</style>



