<template>
   <div class="BxArea">
      <div class="bxButton">
         <v-btn @click="login">Login</v-btn>
         <v-btn @click="logout">Logout</v-btn>
         <v-btn @click="JoinChannel">Join</v-btn>
         <v-btn @click="LeaveChan">Leave</v-btn>
      </div>
      <div class="BxChatbox">
         <div class="BxAvatarChat">
            <div class="AvatarChat">
               <v-avatar size = "80">
                  <img
                     src="../../assets/logo.png"
                     alt="avatar"
                  >
               </v-avatar>
            </div>
            <div class="NameAvatar">
               <p>{{account}}</p>
               <span>{{status}}</span>
            </div>
         </div>
         <div class="BxChatList" id="BxChatList">
            <div class="ChatList" v-for="(n, i) in MsgText" :key="i">
               <div>
                  <div class="BxMessage" :class="n.status">
                    {{n.message}}
                  </div>
               </div>
               
               <span class="bxcaption" > {{n.status}}</span>
            </div>
         </div>
         <div class="BXChatInputAgora">
            <div class="ChatInput">
               <input type="text" v-model="TextIn" placeholder="Type some messages... ">
               <v-icon @click="broadcastMessage" color="primary">send</v-icon>
            </div>
            <div class="ICHelp">
              
                  <v-tooltip slot="append" :value="true" top>
                     <v-icon  slot="activator" color="primary" dark>help</v-icon>
                     <span  style="align-text:center;">Click to reload messenger</span>
                  </v-tooltip>
            
            </div>
         </div>
      </div>
     
   </div>
</template>

<script>
   import Signal from 'agora-signaling-sdk'
   import {AGORA_APP_ID, APP_CERTIFICATE} from '../../components/AgoraConfig/agoraConfig'
   export default {
      data(){
         return{
            AppId:null,
            signal:null,
            Session:null,
            Channel:null,
            account:'BAMBANG',
            status:'coach',
            uid:null,
            IDChannel:'HALOOS',
            token:'_no_need_token',
            MsgText:[],
            TextIn:''
            
         }
      },
      created() {
         this.AppId = AGORA_APP_ID
         this.signal = Signal(this.AppId)
         // eventBus.$emit('get-channelEmitter', this.channelEmitter)
         // eventBus.$emit('get-sessionEmitter', this.sessionEmitter)

        
      },
      mounted() {
          
         // const account = this.Account;
         // const token ='_no_need_token'
         // return new Promise((resolve, reject) => {
         //    this.Session = this.signal.login(account, token)
         //    // Proxy callback on session to sessionEmitter
         //    this.Session.onLoginSuccess = () => {
         //       return new Promise((resolve, reject) => {
         //          if(this.Session === null || !this.Session){
         //             console.log('session must be initialized before joining channel')
         //          }else{
         //             this.Channel = this.Session.channelJoin(this.IDChannel)

         //             this.Channel.onChannelJoined = () =>{
         //                 console.log("Join channel Chat Success")
         //                 console.log(this.Channel)
         //             }

         //             this.Channel.onChannelJoinFailed = () =>{
         //                 console.log("Join channel Chat Failed")
         //             }
         //          }
         //       })

         //    }

         //    this.Session.onLoginFailed = () => {
         //          console.log('Login failed Chat success')
         //    }
         //    console.log(this.Session)
            
         // })
         
      },
      methods: {
         login(){
            this.Session = this.signal.login(this.account, this.token)

            this.Session.onLoginSuccess = () =>{
               console.log('Login Success')
            }

            this.Session.onLoginFailed = () => {
               console.log('Login failed')
            }
         },

         JoinChannel(){
            if(this.Session === null || !this.Session){
                this.login()
            }else{
               this.Channel = this.Session.channelJoin(this.IDChannel)
               this.Channel.onChannelJoined = () =>{
                     console.log("Join channel Chat Success")
                     console.log(this.Channel)

                     this.MReceive()
               }

               this.Channel.onChannelJoinFailed = () =>{
                     console.log("Join channel Chat Failed")
               }
            }
         },

         MReceive(){
            this.Channel.onMessageChannelReceive = (account, uid, msg) => {
               this.MsgText.push(msg)
            }
         },

         broadcastMessage(msg){
            msg = {
               account: this.account, 
               status: this.status, 
               message: this.TextIn
            }
            this.Channel.messageChannelSend(msg);
            this.TextIn = ''
         },

         logout() {
            if(this.Session !== null || this.Session){
               this.Session.logout();
               this.Session.onLogout = () => {
                  console.log('lOGoUT Success')
               }
            }
         },

         LeaveChan(){
            if(this.Channel !== null){
               this.Channel.channelLeave();

               this.Channel.onChannelLeaved = () =>{
                  console.log("Leave Channel Chat success")
               }

            }
         }
      },
      watch: {
         MsgText(val){
           if(!val)
            return
            else if(val){
               console.log(val)
            }
         }
      },
      beforeDestroy() {
         if(this.Channel !== null){
            this.Channel.channelLeave();

            this.Channel.onChannelLeaved = () =>{
               console.log("Leave success")
            }

         }
      },
   }
</script>


<style lang="scss" scoped>
   .BxArea{
      display: flex;
      display: flex;
      align-items: center;
   }

   .bxButton{
      width: 30%;
   }
    
   .v-input--is-disabled:not(.v-input--is-readonly) {
    pointer-events: none;
   }
   .classAc{
      border:1px solid red;
   }
   #chat-container{
      margin-top: 30px;
     
      background-color: #fff;
      min-height: 300px;
      display: flex;
      flex-direction: column;

   }

   .list-group{
     
      min-height: 200px;
   }

   .input-area{
     
     display: flex;
     align-items: center;
     
   }

   .chatBox{
      border: .5px solid;
      padding:8px;
   }
  

   ul {
  list-style-type: none;
}
   .form-control{
      padding:5px;
     
      width: 80%;
   }
   
     .BxChatbox{
         height: 498px;
         display: flex;
         flex-direction: column;
         background-color:$white;
         margin: 60px auto;
         width: 100%;
         border-radius: 10px;
         box-shadow: 0px 0px 5px 1px rgba(115, 115, 115, 0.27);
         p{
            margin-bottom: 0px;
         }

         .BxAvatarChat{
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top:-39px;
            border-bottom:.5px solid $borderGrey;

            .AvatarChat{
               margin-bottom: 10px;
               cursor: pointer;

               .v-avatar{
                  box-shadow: 0px 0px 10px 2px rgba(115, 115, 115, 0.27);
               }
            }
            .NameAvatar{

               margin-bottom: 20px;
               display: flex;
               flex-direction: column;
               align-items: center;

               p{
                  color: $darkblue;
                  font-size: 1.1em;
                  font-weight:600;
               }

               span{
                  color: $darkgrey;
                  font-size: .9em;
               }
            }
         }

         .BxChatList{
            //border:.5px solid $borderGrey;
            min-height: 300px;
            max-height: 300px;
            overflow-y: scroll;
            padding: 10px;
            display: flex;
            flex-direction: column;

            .ChatList{
               //border:1px solid;
               display: flex;
               flex-direction: column;
               .BxMessage{

                  font-size: .9em;
                  display: inline-block;
                  padding: 10px 18px;
                  position: relative;
                  vertical-align: top;
               }


              .me {
                  float: left;
                  margin: 5px 45px 15px 20px;
                  background-color: #F2F2F2;
                  border-radius: 27px 25px 25px 0;

               }

               .you {
                  float: right;
                  margin: 5px 20px 15px 45px;
                  background-color: #02669A;
                  color: #fff;
                  border-radius: 25px 27px 0px 25px;
               }

               .bxcaption{
                  font-size: .8em;
                  width:30px;
                  margin-top:-15px;
                  color:#9ba8cb8f;
               }

               .mes {
                  float: left;
                  display: flex;
                  align-self: flex-start;
                  margin-left:20px;
               

               }
               .yous {
                  float: right;
                  display: flex;
                  align-self: flex-end;
                  margin-right:20px;
               }



            }
         }

         .BXChatInput{

            height: 100px;
            padding: 15px;
            .ChatInput{
               border:.5px solid $darkgrey;
               height: 50px;
               border-radius: 30px;
               display: flex;
               align-items: center;
               justify-content: center;
               overflow: hidden;
               input{
                  width: 80%;
                  height: 90%;
                  font-size: 1.1em;
                  outline: none;
               }
               ::placeholder{
                  color:#9a9a9aad;
               }
               span{
                  width: 10%;
                  height: 90%;
                  font-size: 1.2em;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  margin-left: 5px;
                  cursor: pointer;
                  color:$bgDarkBlue;
               }
            }
         }

         .BXChatInputAgora{

            height: 100px;
            padding: 15px;
            display: flex;
            justify-content: space-around;
            align-items: center;
            .ChatInput{
               border:.5px solid $darkgrey;
               height: 50px;
               border-radius: 30px;
               display: flex;
               align-items: center;
               justify-content: center;
               overflow: hidden;
               width: 86%;
               input{
                  width: 80%;
                  height: 90%;
                  font-size: 1.1em;
                  outline: none;
               }
               ::placeholder{
                  color:#9a9a9aad;
               }
               span{
                  width: 10%;
                  height: 90%;
                  font-size: 1.2em;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  margin-left: 5px;
                  cursor: pointer;
                  color:$bgDarkBlue;
               }
            }

            .ICHelp{
               cursor: pointer;
               svg{
                  fill:#375197;
               }
            }
         }

      }

</style>



