<template>
   <div>
      <!-- <v-btn @click="login">Login</v-btn>
      <v-btn @click="Logout">Logout</v-btn>
      <v-btn @click="joinChannel">Join</v-btn>
      <v-btn @click="LeaveChan">Leave</v-btn> -->
      <div class="BXscripts__BxChatbox">
         <div class="BXscripts__BxChatbox__BxAvatarChat">
            <div class="AvatarChat" @click="Modal_Picture">
               <v-avatar size = "80">
                  <img
                     :src="this.GET_DataUpcoming.avatar"
                     alt="avatar"
                  >
               </v-avatar>
            </div>
            <div class="NameAvatar" v-if="this.AuthCoach">
               <p>{{this.GET_DataUpcoming.student_name}}</p>
               <span>Student</span>
            </div>
            <div class="NameAvatar" v-else>
               <p>{{this.GET_DataUpcoming.coach_name}}</p>
               <span>Coach</span>
            </div>
         </div>
         <div class="BXscripts__BxChatbox__BxChatList" id="BxChatList" ref="BxChatListArea">
            <div class="ChatList" v-for="(n, i) in MsgText" :key="i">
               <div>
                  <div class="BxMessage" :class="n.status">
                     {{n.message}}
                  </div>
               </div>

               <span class="bxcaption" :class="n.class">{{n.status}}</span>
            </div>
         </div>
         <div class="BXscripts__BxChatbox__BXChatInputAgora">
            <div class="ChatInput">
               <input @keyup.enter="broadcast" type="text" v-model="TextIn" placeholder="Type some messages... ">
            
            </div>
            <div class="ICHelp" :disabled="TextIn==''">
               <v-icon @click="broadcast" color="primary">send</v-icon>
            </div>
         </div>
      </div>

      <v-dialog v-model="DialogUpAvatar" width="250" content-class="DialogUpAvatar">
            <v-card style="border-radius: 3px;">
                <v-avatar size="250">
                    <img :src="this.AvatarUpModal">
                </v-avatar>
            </v-card>
        </v-dialog>
   </div>
</template>

<script>
   import Signal from 'agora-signaling-sdk'
   import {AGORA_APP_ID, APP_CERTIFICATE} from '../../../AgoraConfig/agoraConfig.js'
   import ICHelp from '../../../icons/RoundHelp.vue'
   import InfoIcon from '../../../icons/InfoIcon'

   export default {
      components:{
        ICHelp,
        InfoIcon
      },
      props:[
        'GET_DataUpcoming',
        'AuthCoach'
      ],
      data() {
         return {
            account: '',
            channelName: '',
            session: null,
            appid : AGORA_APP_ID,
            signal : null,
            channel : null,
            token:'_no_need_token',
            TextIn : '',
            MsgText:[],
            DialogUpAvatar: false,
            AvatarUpModal : null,

         }
      },
      created() {
         this.signal = Signal(this.appid)
         this.channelName = this.$store.state.StartLiveSession.session
         // this.channelName = "test"
         this.account = this.$store.state.DataAdditional.username
      },
      mounted() {
         this.login()
      },
      methods: {

         login() {
            this.session = this.signal.login(this.account, this.token)

            this.session.onLoginSuccess = () =>{
               // console.log('Login Success')

               this.joinChannel()
            }

            this.session.onLoginFailed = () => {
               console.log('Login failed')
            }
         },

         Logout(account){
            if(this.session !== null || this.session){
               this.session.logout();
               this.session.onLogout = () => {
                  console.log('lOGoUT Success')
               }
            }
         },


         joinChannel() {
            if(this.session === null || !this.session){
                this.login()
            }else{
               this.channel = this.session.channelJoin(this.channelName)
               this.channel.onChannelJoined = () =>{
                     // console.log("Join channel Chat Success")
                     // console.log(this.channel)

                     this.MReceive()
               }

               this.channel.onChannelJoinFailed = () =>{
                     console.log("Join channel Chat Failed")
               }
            }
         },


         LeaveChan(){
            if(this.channel !== null){
               this.channel.channelLeave();

               this.channel.onChannelLeaved = () =>{
                  console.log("Leave Channel Chat success")
               }

            }
         },

         MReceive(){
            this.channel.onMessageChannelReceive = (account, uid, msg) => {
               this.MsgText.push(msg)
               this.scrollToEnd()

               //console.log(msg)
            }
         },

         broadcast(msg) {

            this.login()

            var Classes = null

            if(this.AuthCoach){
               Classes = 'CoachCapt'
            }else{
               Classes = 'StudentCap'
            }
            msg = {
               message: this.TextIn,
               status : this.AuthCoach ? 'coach':'student',
               account: this.account,
               class: Classes
            }

            this.channel.messageChannelSend(msg);
            this.TextIn = ""
            this.scrollToEnd()
         },

         scrollToEnd() {
            this.$nextTick(() => {
               this.$refs.BxChatListArea.scrollTop = 100000;
            });

         },

         BtnRefresh(){
            window.location.reload(true);
         },

         Modal_Picture(avatar){
            this.DialogUpAvatar = true
            this.AvatarUpModal = this.GET_DataUpcoming.avatar
         },

      },

      watch: {
         MsgText(val){
            if (this.session == null)
               return
               else{
                  if(val.length){
                     this.$store.commit('muttCckLengthChat', val.length)
                  }
               }
         }
      },

      beforeDestroy() {
         if(this.channel !== null){
            this.channel.channelLeave();

            this.channel.onChannelLeaved = () =>{
               console.log("Leave success")
            }

         }
      },
   }
</script>

<style scoped>
   .v-input--is-disabled:not(.v-input--is-readonly) {
    pointer-events: none;
   }
   .classAc{
      border:1px solid red;
   }
   #chat-container{
      margin-top: 30px;

      background-color: #fff;
      min-height: 300px;
      display: flex;
      flex-direction: column;

   }

   .list-group{

      min-height: 200px;
   }

   .input-area{

     display: flex;
     align-items: center;

   }

   .chatBox{
      border: .5px solid;
      padding:8px;
   }


   ul {
      list-style-type: none;
   }
   .form-control{
      padding:5px;

      width: 80%;
   }


</style>



